.button {
  position: absolute;

  left: 50%;
  width: 196px;
  height: 50px;
  margin: 0 auto;
  padding: 14px 28px;
  transform: translateX(-50%);
  cursor: pointer;

  font-family: inherit;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #ebd8ff;

  background-color: #4b2a99;
  border: none;
  border-radius: 10.3108px;

  transition: color 250ms cubic-bezier(0.075, 0.82, 0.165, 1) background-color
    250ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

